@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
body {
  overflow: hidden;
  margin: 0;
  padding: 0;
  scrollbar-width: auto;
  /* "auto" or "thin"  */
  scrollbar-color: blue white;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

#style-6::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}

#style-6::-webkit-scrollbar {
  width: 5px;
  background-color: #f5f5f5;
}

#style-6::-webkit-scrollbar-thumb {
  background-color: #49b1ff;
  background-image: -webkit-linear-gradient(45deg, rgba(255, 255, 255, 0.2) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.2) 50%, rgba(255, 255, 255, 0.2) 75%, transparent 75%, transparent);
}

#root {
  margin: 0;
  padding: 0;
  width: 99%;
}

.row {
  height: auto;
}

.row .side {
  background-color: #49b1ff;
  overflow-x: hidden;
}

.row .side ul {
  list-style: none;
  text-align: center;
  margin-left: 10px;
}

.row .side ul li img {
  width: 30%;
}

.row .side ul li {
  margin-top: 30px;
  padding: 10px 0px;
}

.row .side ul .drop {
  margin-bottom: 90px;
}

.row .main {
  background-color: #eceef9;
}

p {
  font-weight: 900;
  color: #4d4f59;
  padding: 0;
  margin: 0;
}

a {
  background-color: transparent;
  border-radius: 100px;
}

@media (max-width: 840px) {
  a {
    background-color: #49B1FF;
    border-radius: 100px;
  }
}

.user-img {
  width: 30px;
}

@media (max-width: 840px) {
  .user-img {
    background-color: white;
    padding: 3px;
  }
}

.user-btn {
  background-color: white;
  border: none;
}

@media (max-width: 840px) {
  .user-btn {
    background-color: #49B1FF;
  }
}

.main-img {
  position: relative;
  width: 100%;
}

.select-option {
  width: 100%;
  text-align: center;
  font-size: 20px;
  padding: 10px;
  font-family: "Open Sans", sans-serif;
}

.footer {
  text-align: center;
  background-color: white;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.footer p {
  padding: 0;
  margin: 0;
}

.create-event:hover {
  border-right: solid #eceef9 5px;
  background-color: #62bbff;
  transition: ease-in 0.1s;
}

.right {
  padding: 15px;
  padding-top: 25%;
  height: 100px;
}

.right p {
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
}

.right .dot {
  width: 20px;
}

.event-card {
  padding: 15px;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 0px 7px -4px rgba(0, 0, 0, 0.75);
  margin-top: 15px;
}

.event-card p {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  color: #000000;
  margin-bottom: 2px;
  font-size: 14px;
}

.event-card .number {
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.event-card .number p {
  position: relative;
  font-size: 30px;
}

.event-card img {
  width: 40px;
}

.sub-topic p {
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  padding-left: 15px;
  font-size: large;
  color: #4d4f59;
  margin: 0;
}

.light-sub-topic {
  font-family: "Open Sans", sans-serif;
  font-weight: lighter;
  padding-left: 15px;
  padding-top: 10px;
}

form {
  padding: 0px 10px 0px 20px;
}

@media (max-width: 840px) {
  form {
    overflow-x: hidden;
  }
}

.form-card {
  background-color: white;
  padding: 25px 25px;
  height: auto;
  margin: 0;
}

.form-card .right-side {
  height: auto;
}

.form-card .right-side input {
  width: 100%;
  background-color: #eceef9;
  border: none;
  outline: none;
  padding: 2px;
  border-radius: 10px;
  padding-left: 15px;
  font-weight: 13px;
}

.form-card .right-side select {
  width: 100%;
  background-color: #eceef9;
  border: none;
  outline: none;
  padding: 2px;
  border-radius: 10px;
  padding-left: 15px;
}

.form-card .right-side .row {
  height: auto;
  padding-top: 15px;
}

.form-card .left-side {
  height: auto;
}

.form-card .left-side .row {
  height: auto;
  padding-top: 15px;
}

.form-card .left-side input {
  width: 100%;
  background-color: #eceef9;
  border: none;
  outline: none;
  padding: 2px;
  border-radius: 10px;
  padding-left: 15px;
}

.form-card .left-side textarea {
  width: 100%;
  background-color: #eceef9;
  border: none;
  outline: none;
  padding: 2px;
  border-radius: 10px;
  padding-left: 15px;
  height: 110px;
}

.main-row {
  height: auto;
}

.chart {
  width: 100%;
  margin-top: 10%;
  padding: 0px;
}

.chart p {
  text-align: center;
  padding: 10px 0px 10px 0px;
  background-color: #eceef9;
  margin-top: 10px;
  border-radius: 10px;
}

.chart canvas {
  padding: 0;
  margin: 0;
}

.slide {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100vh;
  padding: 0;
}

.fix-right {
  position: fixed;
}

@media (max-width: 840px) {
  .fix-right {
    display: none;
  }
}

.item {
  height: auto;
  margin-top: 10px;
}

.item input {
  width: 100%;
  background-color: #eceef9;
  border: none;
  outline: none;
  padding: 2px;
  border-radius: 10px;
  padding-left: 15px;
  text-align: center;
}

.item a {
  color: white;
}

.select-item {
  height: auto;
  padding: 20px;
}

.select-item .row {
  height: auto;
}

.select-item select {
  width: 100%;
  background-color: #eceef9;
  border: none;
  outline: none;
  padding: 2px;
  border-radius: 10px;
  padding-left: 15px;
}

.btn {
  padding: 2px;
}

.upload {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
}

.upload form {
  height: auto;
}

.upload input[type="file"], .upload button {
  background-color: #eceef9;
  width: 90%;
  padding: 10px;
  border-radius: 10px;
}

.upload img {
  width: 50%;
}

.upload .row {
  height: 75px;
}

.emai {
  width: 100%;
}

.email-div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.upload-email {
  height: auto;
}

.adddet {
  width: 100%;
}

.adddet input {
  width: 100%;
}

.save-event {
  margin: 20px 0px 20px 0px;
  width: 100%;
  padding: 10px;
}

.dropdown-item {
  border-radius: 1px;
}

.maincont {
  height: 100vh;
}

@media (max-width: 840px) {
  .maincont {
    height: 110vh;
  }
}

.maincont-img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

@media (max-width: 840px) {
  .maincont-img {
    margin-top: 50px;
    width: 90%;
  }
}
/*# sourceMappingURL=index.css.map */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav {
  width: 100%;
  height: 70px;
}

nav .nav-head {
  font-weight: bolder;
  font-size: 1.9rem;
  line-height: 80px;
}

nav ul {
  float: right;
  margin-right: 20px;
}

nav ul li {
  display: inline-block;
  line-height: 80px;
  margin: 0px 5px;
}

.checkbtn {
  font-size: 30px;
  color: white;
  float: right;
  line-height: 80px;
  margin-right: 10px;
  cursor: pointer;
  display: none;
}

#check {
  display: none;
}

@media (max-width: 810px) {
  .event-side-nav {
    display: none;
  }
}

label.logo {
  font-weight: bolder;
  font-size: 30px;
  padding-left: 20px;
  line-height: 80px;
}

.nav-items {
  display: none;
}

@media (max-width: 952px) {
  .nav-items {
    display: block;
  }
  label.logo {
    font-size: 29px;
    padding-left: 20px;
    font-weight: bolder;
  }
  nav ul li a {
    font-size: 16px;
  }
}

@media (max-width: 858px) {
  .checkbtn {
    display: block;
  }
  ul {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: #2c3e50;
    top: 70px;
    left: -100%;
    text-align: center;
    transition: all .5s;
    z-index: 5;
  }
  nav ul li {
    display: block;
    margin: 50px 0;
    line-height: 30px;
    color: white;
  }
  nav ul li a {
    font-size: 20px;
  }
  a:hover, a.active {
    background: none;
    color: #0082e6;
  }
  #check:checked ~ ul {
    left: 0;
  }
}

.fas {
  color: black;
  width: 50%;
}
/*# sourceMappingURL=nav.css.map */
